import numeral from 'numeral';

numeral.register('locale', 'ge', {
    delimiters: {
        thousands: ' ',
        decimal: ','
    },
    abbreviations: {
        thousand: 'k',
        million: 'm',
        billion: 'b',
        trillion: 't'
    },
    ordinal : function (number) {
        return number === 1 ? 'er' : 'me';
    },
    currency: {
        symbol: ''
    }
});
numeral.locale('ge');